o al 5%, lo que deja entrever que a la empresa que oferta este producto cada vez le es más difícil conseguir mejores resultados de ventas, es decir, mayor crecimiento, esta condición hace que esté ubicado en el cuadrante de las vacas lecheras donde el deber ser del empresario es mantener su participación en el mercado y propender por aumentar el crecimiento en ventas, sino defenderlo.")])])])]),_c('div',{attrs:{"x":"37%","y":"40%","tooltip":"02","numero":"2"}},[_c('div',{staticClass:"row"},[_c('div',{staticClass:"col-lg-3 mb-4 mb-lg-0"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/tema3/21.svg")}})])]),_c('div',{staticClass:"col-lg-9"},[_c('p',[_vm._v("Este producto 2 presenta un nivel de participación en el mercado alto, por encima de sus competidores cercanos, con un fuerte crecimiento en ventas lo que lo caracteriza como un producto en la fase de crecimiento. Este es un producto que en magnitud puede manejar más volúmenes que el producto número uno, sin embargo, aún se encuentra en la etapa de crecimiento y por ser mayor al 10%, se presume que podría mejorar durante un tiempo. Luego, la decisión estratégica es mantener su inversión con el fin de garantizar este propósito de tal manera que cuando llegue a su etapa de madurez goce de las mejores condiciones de ventas y de participación en el mercado, donde seguramente será fuente de grandes beneficios para la empresa.")])])])]),_c('div',{attrs:{"x":"63%","y":"23%","tooltip":"03","numero":"3"}},[_c('div',{staticClass:"row"},[_c('div',{staticClass:"col-lg-3 mb-4 mb-lg-0"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/tema3/22.svg")}})])]),_c('div',{staticClass:"col-lg-9"},[_c('p',[_vm._v("El producto 3 ubicado en el cuadrante de productos de interrogación, es quizá el producto más joven en la organización, su cuota de participación en el mercado está por debajo de lo que hace su competencia, sin embargo, las ventas demuestran un crecimiento superior al 15%, lo que deja entrever un certero producto que entrará a su fase de crecimiento y ampliación por ende de la participación en el mercado.")])])])])])],1),_c('p',{staticClass:"mt-5",attrs:{"data-aos":"fade-up"}},[_vm._v("A partir de la ubicación de estos productos del portafolio de la empresa, se deben establecer las medidas estratégicas para el sostenimiento, ampliación y defensa de estos productos, de continuar siendo atractivos y rentables para la empresa. En caso del ejemplo es evidente que los 3 productos deben continuar y que en especial los productos dos y tres deben ser tratados con acciones que les permita continuar su desarrollo y crecimiento, mientras que en el producto número uno las iniciativas buscarán la defensa de la participación en el mercado y la sostenibilidad del nivel de ventas en el largo plazo.")]),_vm._m(23),_c('p',{staticClass:"mt-4",attrs:{"data-aos":"fade-up"}},[_vm._v("Es la representación gráfica de las diferentes fases por las que atraviesan los productos desde su desarrollo hasta su retiro de la oferta del portafolio de productos de la empresa; Está constituido por cuatro fases plenamente identificables.")]),_c('p',{staticClass:"mt-4",attrs:{"data-aos":"fade-up"}},[_vm._v("Reconozcamos en qué consisten cada fase:")]),_c('TabsC',{staticClass:"color-acento-botones mt-5"},[_c('div',{staticClass:"pt-5",staticStyle:{"background-color":"#D9E9F1"},attrs:{"titulo":"Fase de introducción"}},[_c('div',{staticClass:"row p-4 pt-0"},[_c('div',{staticClass:"col-lg-8 mb-4 mb-lg-0"},[_c('p',[_vm._v("A nivel comercial, todo producto inicia en el lanzamiento al mercado, se trata de una novedad para los clientes o consumidores, porque desconocen su desempeño, características, etc., lo que implica que las ventas sean bajas al principio, especialmente, si la empresa o marca asociada no es muy conocida; por ello requiere de inversiones iniciales en estrategias de marketing (promoción y publicidad) para su lanzamiento y distribución, colocación en tiendas, etc., de manera que tenga llegada 