 no aportar a la empresa beneficios ni utilidades, sino que exclusivamente cubren sus costos o llegan inclusive a ser subsidiados por la misma empresa. Sea cual fuere la naturaleza del declive en ventas hay dos opciones:")]),_c('ul',{staticClass:"lista_2_1 lista-ul--color mt-4"},[_c('li',{staticClass:"mb-1",attrs:{"data-aos":"fade-up"}},[_c('i',{staticClass:"lista-ul__vineta"}),_vm._v("Renovar el producto")]),_c('li',{attrs:{"data-aos":"fade-up"}},[_c('i',{staticClass:"lista-ul__vineta"}),_vm._v("Retirar el producto totalmente de la oferta del portafolio y del mercado en sí.")])]),_c('p',{staticClass:"mt-5"},[_vm._v("Esta decisión debe tomarla la empresa en función de las necesidades del público y de identificar si no es rescatable, ya que está en declive, lo que quiere decir que viene de grandes ventas.")])]),_c('div',{staticClass:"col-lg-4"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/tema3/26.png")}})])])])])]),_c('p',{staticClass:"mt-5",attrs:{"data-aos":"fade-up"}},[_vm._v("En la siguiente figura, podemos observar reflejadas las 4 etapas del ciclo de vida de un producto.")]),_vm._m(24),_vm._m(25),_c('p',{staticClass:"mt-4",attrs:{"data-aos":"fade-up"}},[_vm._v("En la siguiente figura, se puede correlacionar la relación existente entre el ciclo de vida del producto y la matriz BCG identificando como el crecimiento en ventas se relaciona con la participación de mercado y el tiempo, con la tasa de crecimiento en ventas, lo que sirve para alinear las decisiones estratégicas en la empresa según el tipo de mercado, su clasificación y su ciclo de vida de producto.")]),_vm._m(26),_c('p',{staticClass:"mt-5",attrs:{"data-aos":"fade-up"}},[_vm._v("La capacidad competitiva de un producto o servicio se debe correlacionar con su ciclo de vida en el que atraviesa al momento del diagnóstico con el fin de establecer las posibilidades estratégicas y de decisión. Un producto con buena participación en el mercado y un crecimiento sostenible en ventas, puede corres